@import url('https://fonts.googleapis.com/css?family=Noto+Sans:400,400i,700');

body {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  font-family: "Noto Sans", Arial, sans-serif;
}

@media (min-width: 1024px) {
  body {
    background-image: url("./assets/trumpBackground.jpg");
    background-color: rgb(230, 236, 240);
    background-position: bottom;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
  }
}


a {
  text-decoration: none;
  color: rgb(0, 172, 237);
  font-weight: 400;
}

a:hover {
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
}

.root {
  overflow-x: hidden;
}

.App {
  overflow-x: hidden;
}

.timelineHeader {
  width: 100%;
  height: 146px;
  background-color: rgb(255, 255, 255);
  border: 1px solid rgba(230, 236, 240, 0.01);
}

@media (min-width: 1024px) {
  .timelineHeader {
    max-width: 590px;
  }
}

.timelineHeaderTitle {
  font-size: 30px;
  line-height: 30px;
  font-weight: 700;
  padding: 20px 15px 0px 15px;
}

.timelineHeaderSubtitle {
  font-size: 16px;
  line-height: 16px;
  color: rgb(101, 119, 134);
  padding: 10px 0 0 0;
  font-weight: 400;
}

.timelineHeaderLinkList {
  display: block;
  background-color: rgb(255, 255, 255);
  border-bottom: 1px solid rgb(230, 236, 240);
  width: 100%;
  margin: 0;
  padding: 0;
  top: 0;
  z-index: 10;
  color: rgb(20, 23, 26);
}

@media (min-width: 1024px) {
  .timelineHeaderLinkList {
    max-width: 590px;
  }
}

.timelineHeaderLink {
  display: inline-block;
  position: relative;
  color: rgb(0, 172, 237);
  font-size: 18px;
  font-weight: 700;
  line-height: 18px;
  padding: 20px 15px 20px 15px;
  cursor: pointer;
  text-decoration: none;
  list-style-position: outside;
  list-style-type: none;
}

.timelineHeaderLink:after {
  content: "";
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 0px;
  background-color: rgb(0, 172, 237);
  -webkit-transition: all 250ms ease-in-out;
  -o-transition: all 250ms ease-in-out;
  transition: all 250ms ease-in-out;
}

.timelineHeaderLink:hover:after {
  height: 4px;
}

.isActiveLink {
  color: rgb(20, 23, 26);
  cursor: default;
}

.isActiveLink:after {
  height: 4px;
}

.socketStatusWrapper {
  position: fixed;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  padding: 7px 15px 7px 15px;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: end;
      justify-content: flex-end;
  z-index: 999;
  font-size: 12px;
  line-height: 12px;
  font-weight: 400;
  background-color: rgb(255, 255, 255);
  bottom: 0px;
  border-top: 1px solid rgb(230, 236, 240);
}

@media (min-width: 1024px) {
  .socketStatusWrapper {
    max-width: 590px;
  }
}

.socketStatusIndicator {
  position: relative;
  display: inline-block;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  margin-left: 10px;
}

#socketConnected {
  background-color: rgba(92, 231, 99, 1.0);
  border: 2px solid rgba(79, 198, 85, 1.0);
}

#socketConnecting {
  background-color: rgba(255, 240, 140, 1.0);
  border: 2px solid rgba(255, 231, 76, 1.0);
}

#socketDisconnected {
  background-color: rgba(255, 174, 178, 1.0);
  border: 2px solid rgba(255, 143, 150, 1.0);
}


.tweetTimeline {
  color: rgb(20, 23, 26);
  background-color: rgb(255, 255, 255);
  font-size: 14px;
  line-height: 19.25px;
  text-align: left;
  display: block;
  position: relative;
  width: 100%;
  min-height: 100vh;
  padding-bottom: 31px;
  margin-left: auto;
  margin-right: 0;
}

@media (min-width: 1024px) {
  .tweetTimeline {
    margin-right: 10%;
    max-width: 590px;
  }
}

.settingsView {
  background-color: rgb(255, 255, 255);
  width: 100%;
  padding: 15px;
}

.settingFormTitle {
  font-size: 14px;
  font-weight: 700;
  text-align: left;
  display: block;
}

.settingFormWrapper {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: start;
      align-items: flex-start;
}

.applyButton {
  border-radius: 0px;
  background-color: rgb(0, 172, 237);
  border: 1px solid rgb(0, 172, 237);
}

.applyButton:hover, .applyButton:active, .applyButton:focus {
  background-color: rgb(0, 150, 204);
  border: 1px solid rgb(0, 150, 204);
}

.textBox {
  border-radius: 0px;
  margin-right: 15px;
}

.settingFormDescription {
  display: block;
  font-size: 12px;
  font-weight: 400;
  margin: 5px 0px 10px 0px;
}

.aboutView {
  width: 100%;
  padding: 15px;
}

.aboutHeader {
  font-size: 16px;
  font-weight: 700;
  text-align: left;
  display: block;
  margin: 10px 0px 10px 0px;
}

.aboutBodyText {
  display: block;
  font-size: 14px;
  font-weight: 400;
  margin: 5px 0px 10px 0px;
}

.aboutSpacer {
  display: block;
  width: 100%;
  border-bottom: 1px solid rgb(230, 236, 240);
  margin-top: 20px;
  margin-bottom: 20px;
}

.tweetWrapper {
  background-color: rgb(255, 255, 255);
  border-left: 1px solid rgb(230, 236, 240);
  border-right: 1px solid rgb(230, 236, 240);
  display: block;
  position: relative;
  margin: 0;
}

.tweetOuterContent {
  border-bottom: 1px solid rgb(230, 236, 240);
  cursor: pointer;
  display: block;
  min-height: 51px;
  padding: 9px 12px 9px 12px;
  position: relative;
}

.tweetOuterContent:hover {
  background-color: rgb(245, 248, 250);
}

.tweetContent {
  margin-left: 58px;
  display: block;
}

.tweetHeader {
  display: -ms-flexbox;
  display: flex;
}

.accountGroup {
  cursor: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-negative: 1;
      flex-shrink: 1;
  margin-right: 5px;
  overflow: hidden;
  text-decoration: none;
  color: rgb(20, 23, 26);
}

.accountGroup:hover .accountName {
  color: rgb(0, 172, 237);
  text-decoration: underline;
}

.accountGroup:hover .accountUsername {
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
}

.accountPicture {
  border-radius: 50%;
  border: 0px solid rgb(20, 23, 26);
  float: none;
  height: 48px;
  list-style-image: none;
  list-style-position: outside;
  list-style-type: none;
  margin-left: -58px;
  margin-top: 3px;
  position: absolute;
  width: 48px;
}

.accountName {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal;
  font-weight: 700;
  word-break: break-all;
  padding-right: 3px;
}

.accountUsername {
  color: rgb(101, 119, 134);
  text-decoration: none;
}

.tweetText {
  white-space: pre-wrap;
  word-wrap: break-word;
  color: rgb(20, 23, 26);
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  margin: 0;
}

.tweetLink {
  text-decoration: none;
  color: rgb(0, 172, 237);
  font-weight: 400;
}

.tweetLink:hover {
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
}

.grammarText {
  display: inline;
  position: relative;
  background-color: rgba(255, 231, 76, 0.65);
  border-top-right-radius: 2px;
  border-top-left-radius: 2px;
  border-bottom: 2px solid rgba(234, 212, 70, 1.0);
  padding-left: 3px;
  padding-right: 3px;
}

.styleText {
  display: inline;
  position: relative;
  background-color: rgba(73, 189, 255, 0.5);
  border-top-right-radius: 2px;
  border-top-left-radius: 2px;
  border-bottom: 2px solid rgba(73, 189, 255, 1.0);
  padding-left: 3px;
  padding-right: 3px;
}

.spellingText {
  display: inline;
  position: relative;
  background-color: rgba(255, 89, 100, 0.5);
  border-top-right-radius: 2px;
  border-top-left-radius: 2px;
  border-bottom: 2px solid rgba(255, 89, 100, 1.0);
  padding-left: 3px;
  padding-right: 3px;
}

.tweetFooter {
  display: block;
  height: 20px;
}

/* Custom Popover Styles */
.popover-content {
  padding: 5px;
}

.popover-title {
  font-weight: 700;
  font-size: 16pt;
  padding: 5px;
}

.fade {
  opacity: 1.0;
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
}

/* Grammar errors */
#popoverGrammar {
  background-color: rgba(255, 240, 140, 1.0);
  border: 0px;
  padding: 0px;
}

#popoverGrammar .arrow:after {
  border-top-color: rgba(255, 240, 140, 1.0);
}

#popoverGrammar .arrow {
  border-top-color: rgba(255, 231, 76, 1.0);
}

#popoverGrammar .popover-title {
  background-color: rgba(255, 231, 76, 1.0);
  border: 0px;
}

/* Spelling errors */
#popoverSpelling {
  background-color: rgba(255, 174, 178, 1.0);
  border: 0px;
  padding: 0px;
}

#popoverSpelling .arrow:after {
  border-top-color: rgba(255, 174, 178, 1.0);
}

#popoverSpelling .arrow {
  border-top-color: rgba(255, 143, 150, 1.0);
}

#popoverSpelling .popover-title {
  background-color: rgba(255, 143, 150, 1.0);
  border: 0px;
}

/* Style errors */
#popoverStyle {
  background-color: rgba(165, 222, 255, 1.0);
  border: 0px;
  padding: 0px;
}

#popoverStyle .arrow:after {
  border-top-color: rgba(165, 222, 255, 1.0);
}

#popoverStyle .arrow {
  border-top-color: rgba(112, 203, 255, 1.0);
}

#popoverStyle .popover-title {
  background-color: rgba(112, 203, 255, 1.0);
  border-bottom: 0px;
}

.errorBox {
  position: fixed;
  width: 100%;
  z-index: 999;
  font-size: 12px;
  line-height: 12px;
  font-weight: 400;
  bottom: 31px;
}

@media (min-width: 1024px) {
  .errorBox {
    max-width: 590px;
  }
}

.errorWrapper {
  position: relative;
  display: block;
  width: 100%;
  background-color: rgba(255, 174, 178, 0.8);
}

.errorTitle {
  padding: 5px;
  display: block;
  font-weight: 700;
  width: 100%;
  background-color: rgba(255, 143, 150, 0.8);
}

.errorDescription {
  padding: 3px 5px 5px 5px;
}


